.cardContainer {
    border: 1px solid white;
    padding: 7px;
    border-radius: 9px;
    width: 200px;
    margin: 15px;
    display: flex;
    flex-direction: column;
    justify-content: end;
  }
  .cardText {
    color: white;
    margin: 10px 0;
  }
  
  .cardBtn {
    background-color: inherit;
    border-radius: 9px;
    color: white;
    width: 100%;
    outline: none;
    border: 1px solid white;
    padding: 5px;
    margin-top: 15px;
    bottom: 5px;
    cursor: pointer;
  }